import React, { memo, useEffect, useState } from "react";
import { IconAvatar, IconGlobal, IconMenu } from "@/assets/svg";
import { RightWrapper } from "./style";

const HeaderRight = memo(() => {
  // 
  const [showPanel, setShowPanel] = useState(false);

  // 
  useEffect(() => {
    function windowHandleClick() {
      // 
      setShowPanel(false);
    }
    //  window : , DOM 
    window.addEventListener("click", windowHandleClick, false);
    return () => {
      window.removeEventListener("click", windowHandleClick, false);
    };
  }, []);

  // 
  function profileClickHandle(event) {
    setShowPanel(!showPanel); //  or 
    event.stopPropagation(); // 
  }

  return (
    <RightWrapper>
      <div className="btns">
        <span className="btn"></span>
        <span className="btn"></span>
        <span className="btn">
          <IconGlobal />
        </span>
      </div>

      <div className="profile" onClick={profileClickHandle}>
        <IconMenu />
        <IconAvatar />

        {showPanel && (
          <div className="panel">
            <div className="top">
              <div className="item register"></div>
              <div className="item login"></div>
            </div>
            <div className="bottom">
              <div className="item"></div>
              <div className="item"></div>
              <div className="item"></div>
            </div>
          </div>
        )}
      </div>
    </RightWrapper>
  );
});

export default HeaderRight;
